import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';
import Info from './panels/Info';
import Piter from './panels/Piter';
import Anapa from  './panels/Anapa';
import Vladivastok from './panels/Vladivastok';
import Sevastopol from './panels/Sevastopol';
import Simferopol from './panels/Simferopol';
import Kalyga from  './panels/Kalyga';
import Mal from  './panels/Mal';
import Serpyxov from './panels/Serpyxov';
import Novgorod from './panels/Novgorod';
import Novos from './panels/Novos';
import Penza from './panels/Penza';
import Yfa from './panels/Yfa';
import Kazan from './panels/Kazan';








class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			list: {},
			activePanel: 'home',
			fetchedUser: null,
			search:  ''
		};
		this.ChangeSearch   = this.ChangeSearch.bind(this)
}



maps() {
let api = `https://geocode-maps.yandex.ru/1.x/?format=json&geocode=${this.state.search}`
fetch(api)
.then(res => res.json())
.then(data => this.setState({
      list: data.responce.GeoObjectCollection.featureMember
  }))
.catch(e => console.log(e))
}




	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};





      ChangeSearch (e) {
        this.setState({ search:e.replace(/\s+/g,` `) });
        this.maps();
      }




    





	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} />
				<Persik id="persik" go={this.go} />
				<Info id="info" go={this.go} />
				<Piter id="piter" go={this.go} />
				<Anapa id="anapa" go={this.go} />
				<Vladivastok id="vladivastok" go={this.go} />
				<Sevastopol id="sevastopol" go={this.go} />
				<Simferopol id="simferopol" go={this.go} change={this.ChangeSearch} state={this.state} />
				<Kalyga id="kalyga" go={this.go} />
				<Mal id="mal" go={this.go} />
				<Serpyxov id="serpyxov" go={this.go} />
				<Novgorod id="novgorod" go={this.go} />
				<Novos id="novos" go={this.go} />	
				<Penza id="penza" go={this.go} />
				<Yfa id="yfa" go={this.go} />
				<Kazan id="kazan" go={this.go} />
				</View> 
		);
	}
}

export default App;
