import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, Button, Div, platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';

const osname = platform();

const Kazan = props =>(
	<Panel id={props.id}>
	<PanelHeader 
	left={<HeaderButton onClick = {props.go} data-to = "home">
	{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
	</HeaderButton>}
	>
	  Казань
	</PanelHeader>
	<Group>
	<Div>
	1.Казанский Кремль
<br/>
<br/>
Расположенная в историческом центре на холме крепость, имеющая статус музея-заповедника. Объект Всемирного наследия ЮНЕСКО.
После недолгих дебатов ученые пришли к выводу, что первые поселения на территории Казанского Кремля относятся к рубежу X-XI в, возраст Кремля отсчитывают с этих пор. Вход на территорию, ограниченную почти двухкилометровой стеной, свободный.
На территории Кремля находятся:
<br/>
- Башня Сююмбике;
<br/> 
- Благовещенский собор; 
<br/>
- Закладной камень мечети Кул Шариф; 
<br/>
- Комплекс Пушечный двор; 
<br/>
- Мечеть Кул Шариф; 
<br/>
- Памятник зодчим Казанского кремля.
<br/>
- Обзорные площадки
<br/>
<br/>
2.Улица Баумана
<br/>
<br/>
Эта достопримечательность Казани сродни Старому Арбату в Москве. Пешеходная улица в центре города, где можно побродить, посмотреть, перекусить, если проголодался. 
Интересна не только для любителей пеших прогулок. Помимо скульптур и сувениров, интерес представляют фонтаны, в частности, фонтан Водяная "Су Анасы", установленный в 1997 году. Героиню татарской сказки "Су Анасы" часто путают с русалкой.
Так же заслуживают внимания следующие объекты:
<br/>
- ГУМ
<br/>
- Казанский меридиан/ Нулевой километр
<br/>
- Памятник дракону Зиланту
<br/>
- Памятник карете Екатерины II 
<br/>
- Памятник Конь в пальто 
<br/>
- Фонтан Голуби 
<br/>
- Фонтан Лягушки
<br/>
<br/>
2.Петропавловский собор
<br/>
<br/>
Красивейшая архитектурная достопримечательность Казани. Построен в 1723-1726 г.г. и посвящен небесным покровителям Петра Великого Апостолам Петру и Павлу. Строительство было связано с приездом в Казань 27 мая 1722 года Петра Первого.
<br/>
<br/>
3.Памятник казанскому коту
<br/>
<br/>
Некогда по приказу Екатерины II, 30 казанских котов были перевезены в Санкт-Петербург. Произошло это после визита Екатерины в Казань, когда она заметила отсутствие мышей в городе. Трудовые мигранты семейства кошачьих помогли извести грызунов в царских покоях. 
<br/>
В память о событиях тех дней и появились эти два оригинальных достопримечательности-коты-памятники. Один из них теперь находится на улице Баумана, а второй был открыт за несколько месяцев до этого на берегу озера в Раифском монастыре.


	</Div>
	</Group>


 <Group title="Адреса">
            <List>
            
            <Cell component ="a" href ="">Казанский Кремль</Cell>
                        <Cell component ="a" href ="">Улица Баумана</Cell>
                                    <Cell component ="a" href ="">Петропавловский собор</Cell>

            </List>


            </Group>



 <Group title="Фотографии">
            <List>
            
            <Cell component ="a" href ="">Скоро</Cell>
            </List>


            </Group>



	</Panel>
);
